/// Alternative case study styles
@mixin alternative-case-study-card(
    $color-background: $color-red70,
    $color-engaged: $color-red80,
    $color-label: $color-gray2,
    $color-text: $color-purewhite
) {
    display: flex;
    flex-direction: row;

    @include media('<mobile') {
        flex-direction: column;
    }

    @include media('<small') {
        flex-wrap: wrap;
    }

    .card--casestudy--alternative__thumbnail {
        margin-top: $measure--small3;
        margin-bottom: $measure--small3;
        align-self: center;
        width: 14rem;
        overflow: hidden;

        @include media('<mobile') {
            width: auto;
            margin: 0 auto;
            height: 15rem;
        }
        @include media('<small') {
            width: auto;
            margin: 0 auto;
            align-self: center;
            height: 12rem;
        }

        img {
            max-width: inherit;
            width: 100%;
            margin: 0 auto;
            @include media('<mobile') {
                min-height: -webkit-fill-available;
            }
            @include media('<small') {
                width: auto;
                height: 12rem;
            }
        }
    }

    .card--casestudy--alternative__content {
        background-color: $color-background;
        border: 0;
        width: 17rem;
        padding: $measure--small2;
        overflow: hidden;
        transition: background-color $transition-duration ease-out;
        flex-grow: 1;

        @include media('<mobile') {
            width: 100%;
            height: auto;
            min-height: 15rem;
        }

        &:active {
            background: $color-nearblack;
            border-bottom: 0;
        }

        &:hover,
        &:focus {
            border-bottom: 0;
            background: $color-engaged;
        }
    }

    .card--casestudy--alternative__label {
        color: $color-label;
        @include extrasmalltype();
        margin-bottom: $measure--small1;
    }

    .card--casestudy--alternative__title {
        margin-bottom: $measure--small1;

        p {
            @include heading-style();
            @include betatype();
            color: $color-text;
            border-bottom: $border-width-default $border-style-solid $color-text;
            display: inline;
        }
    }

    .card--casestudy--alternative__description {
        p {
            color: $color-text;
            @include smalltype();
        }
    }
}

.card--casestudy--alternative {
    @include alternative-case-study-card();
}
