.card--related-course {
    a {
        display: inline-block;
        @include thumbnail-gradient-scale;
        @include card-shared-properties;
        transition: background-color $transition-duration ease-in;
        $borderStyle: $border-width-hairline $border-style-solid
            rgba($color-nearblack, 0.2);
        border: $borderStyle;

        &:hover {
            @include media('hover') {
                background-color: $color-gray1;
                border: $borderStyle;
            }
        }

        &:active {
            border: $borderStyle;
        }

        &:focus {
            border: $borderStyle;
        }

        @include media('<=small') {
            background-color: $color-gray1;

            &:active {
                background-color: $color-gray1;
            }

            &:focus {
                background-color: $color-gray1;
            }
        }

        img {
            height: 100%;
            width: 100%;
            transition: transform $transition-duration ease-in;

            @include media('reducemotion') {
                transition: none;
            }
        }
    }

    p {
        @include alphatype();
        color: $color-gray10;
    }

    .card__inner {
        margin-bottom: $measure--small5;
        @include card-with-thumbnail;
    }
}

.card--related-course__details {
    width: 100%;
}

.card--related-course__title {
    @include h3style();
    @include betatype();
    border-bottom-width: $border-width-default;
    border-bottom-style: $border-style-solid;
    border-bottom-color: inherit;
}

.card--related-course__type {
    @include smalltype();
    color: $color-red50;
    margin-top: $measure--half;
}

.card--related-course__key-info {
    @include smalltype();
    color: $color-gray7;
    margin-top: $content-element-margin;

    .icon {
        color: $color-red50;
        margin-right: $measure--half;
    }
}

.card--related-course__image {
    margin-right: $measure--small1;
}
