.card--social-media {
    background: $color-purewhite;
    margin-bottom: $measure--small4;
    padding: $measure--small2;
    @include box-shadow;
    transition: box-shadow $transition-duration;

    &:hover {
        @include media('hover') {
            @include box-shadow-darker;

            .card--social-media__source-title {
                background-color: $color-red50;
                color: $color-purewhite;

                span {
                    color: $color-purewhite;
                }
            }
        }
    }

    &:focus,
    &:active {
        @include anchor-default-outline($color-outline);
    }
}

.card.twitter,
.card.instagram,
.card.facebook {
    a {
        &:focus,
        &:active {
            .card--social-media {
                @include outline-properties($color-outline);
            }
        }
    }
}

.card--social-media__category {
    color: $color-gray7;
    font-style: italic;
    padding: $measure--half $measure--basic;
    display: inline-block;
}

.card--social-media__header {
    display: flex;
    h3 {
        @include screenreader-only;
    }

    div:first-child {
        margin-bottom: $measure--half;
    }

    @include media('>mobile') {
        div:first-child {
            margin-right: $measure--basic;
            margin-bottom: 0;
        }
    }
}

.card--social-media__source-title {
    border: $border-width-default $border-style-solid $color-red50;
    border-radius: $measure--basic;
    background-color: $color-purewhite;
    transition: background $transition-duration, color $transition-duration;
    span {
        color: $color-gray10;
        padding: $measure--half;
        transition: color $transition-duration;
    }

    .fa-facebook-f {
        padding: $measure--half 0.65rem;
    }
}

.card--social-media__source__link {
    span {
        color: $color-gray4;
    }
}

.card--social-media__content {
    @include alphatype();

    margin: $measure--small3 0;
    color: $color-gray10;
}

.card--social-media__content--url {
    color: $color-red50;
}

.card--social-media__controls {
    a {
        color: $color-gray7;
        border-bottom: none;
        transition: none;
        margin-right: $measure--small1;

        &:active,
        &:focus,
        &:visited {
            color: $color-gray7;
            background-image: none;
            border-bottom: none;
            background: none;
        }

        &:hover {
            @include media('hover') {
                color: $color-gray7;
                background-image: none;
                border-bottom: none;
                background: none;
            }
        }
    }
}

.card--social-media__time-info {
    color: $color-gray7;
    font-style: italic;
}

.card--social-media__image {
    display: flex;

    img {
        align-self: flex-start;
    }
}

.card--social-media__footer {
    @include smalltype;
    color: $color-gray7;
    margin-top: $measure--basic;
}

.card--social-media-instagram {
    padding: initial;

    .card--social-media__header {
        position: absolute;
        padding-left: $measure--small2;
        padding-top: $measure--small2;
        z-index: z-index('above');
    }

    .card--social-media__footer {
        padding: 0 $measure--small2 $measure--small2 $measure--small2;
    }
}
