.contact-cards {
    padding: $measure--basic $measure--medium7 $measure--basic $measure--medium7;

    @include media('<tablet') {
        @include default-padding;
    }

    @include contact-card();

    &.background--pink80 {
        @include contact-card(
            $color-purewhite,
            $color-purewhite,
            $color-purewhite
        );

        a {
            @include anchor-inline(
                $color-gray4,
                $color-gray4,
                $color-gray11,
                $color-outline--dark
            );
        }
    }
}

.card--contact {
    padding-bottom: $measure--basic;
    background: transparent;
    border-top: $border-width-hairline $border-style-solid
        rgba($color-nearblack, 0.2);

    &:last-child {
        border-bottom: none;
    }
}

.card--contact__name {
    margin-top: $measure--basic;
    font-weight: $font-weight-medium;
}

.card--contact__key-info {
    display: flex;
    word-break: break-word;
    margin-top: $measure--basic;

    span {
        @include alphatype();
    }

    p {
        margin-top: 0;
    }

    .icon {
        margin-right: $measure--half;
        @include alphatype();
        color: $color-red50;

        &.fa-phone {
            transform: rotate(90deg);
        }
    }
}

.card--contact__key-info__wrap {
    @include media('>=tablet') {
        display: flex;
    }

    .card--contact__key-info__wrap__col {
        &:nth-child(1) {
            margin-right: $measure--medium7;
        }
    }
}
