.card--course {
    list-style: none;
    padding: $measure--basic;
    position: relative;
    @include card-shared-properties($color-nearblack, $color-gray8);
    @include background-color-and-transition;

    p {
        &:last-child {
            margin-bottom: 0;
        }
    }

    h3 {
        margin-top: 0;
    }

    .card__inner {
        padding-bottom: 0;
    }
}

.card--course__type {
    color: $color-red50;
    @include smalltype();
    margin-top: $measure--half;
}

p.card--course__key-info {
    @include smalltype();

    .icon {
        color: $color-red50;
        margin-right: $measure--half;
    }
}
