.footer-cards {
    padding: $measure--basic $measure--basic $measure--small4;

    h2 {
        padding-bottom: $measure--half;
    }

    h2:nth-of-type(2n) {
        margin-top: $measure--small4;
    }
}

.footer-cards__contact {
    margin-left: 0;

    li {
        list-style-type: none;

        @include media('<tablet') {
            &:nth-of-type(2n) {
                .footer-cards__contact__name {
                    margin-top: $measure--small4;
                }
            }
        }
    }

    .icon {
        margin-right: $measure--basic;
    }

    .icon--phone {
        transform: rotate(90deg);
    }

    @include media('>=tablet') {
        display: flex;
        justify-content: space-between;
    }
}

.footer-cards__contact__name {
    margin-bottom: $measure--quarter;
}

.footer-cards--pink80 {
    background-color: $color-pink80;
    @include footer-cards--dark;

    h2 {
        @include full-width-heading($color-pink70);
    }
}
