.card--news {
    a,
    a:hover,
    a:active,
    a:focus {
        border-bottom: none;
    }

    &:hover {
        @include media('hover') {
            /* 
                Trigger thumbnail transition - note the following
                values should match the mixin call:

                - scale value
                - background opacity

                Mixin: thumbnail-gradient-scale
            */

            .thumbnail__image-container {
                img {
                    transform: scale(1.04);
                }

                &:after {
                    background-color: rgba(0, 0, 0, 0.1);
                }
            }
        }
    }
}

.card--news__container {
    background: $color-purewhite;
    @include box-shadow;
}

.card--news__image-container {
    @include thumbnail-gradient-scale;

    a:first-child {
        display: block;
    }

    .thumbnail__image-container {
        height: $measure--huge5;
        width: 100%;

        figure {
            margin-top:0;
            height:100%;
        }

        img {
            height: 100%;
            width: 100%;
        }

        @include media('>=tablet') {
            height: 12rem;
        }
    }
}

.card--news__title-link {
    a {
        @include h5style;
        @include underline-transition;
    }
}

.card--news__content {
    padding: $measure--small3;
}

.card--news__description p {
    @include smalltype;
    color: $color-gray10;
}

.card--news__date {
    @include smalltype;
    color: $color-gray10;
    margin-top: $measure--small2;
}

.card--news__category {
    color: $color-gray7;
    font-style: italic;
    padding: $measure--half $measure--basic;
    display: inline-block;
}

.card--news--case-study {
    .card--news__title-link {
        margin-top: $measure--small1;
    }
}

.card--news--research {
    .card--news__content {
        padding: $measure--small3;
    }
}
