.pathway-image-cards__blocks {
    display: grid;
    grid-column-gap: $measure--small4;
    grid-row-gap: $measure--medium7;
    margin-top: $measure--small4;

    @include media('<=small') {
        grid-row-gap: $measure--small4;
    }

    @include media('>=tablet') {
        grid-template-columns: repeat(2, 1fr);
    }

    @include media('>desktop') {
        grid-template-columns: repeat(3, 1fr);
    }
}

.pathway-image-cards {
    @include pathway-image-cards();
    @include default-padding;
    padding-top: $measure--small4;
    padding-bottom: $measure--small4;

    a {
        display: block;
        position: relative;

        &:hover {
            @include media('hover') {
                background-image: none;
            }
        }

        &:active {
            background-image: none;
        }

        &:focus {
            background-image: none;
        }

        img {
            transition: transform $transition-duration ease-in;
            width: 100%;

            @include media('reducemotion') {
                transition: none;
            }
        }
    }
}

.pathway-image-cards {
    &.background--pink80 {
        @include pathway-image-cards(
            $color-purewhite,
            $color-purewhite,
            $color-pink60,
            $color-purewhite,
            $color-purewhite,
            $color-pink60,
            $color-purewhite
        );
    }
}

.card--pathway-image {
    h2,
    h3 {
        @include betatype();
    }
}

.card--pathway-image__icon {
    font-weight: $font-weight-normal;
    margin-left: $measure--quarter;
}

.card--pathway-image__container {
    overflow: hidden;
    margin-bottom: $measure--basic;
}

.card--pathway-image__text-content {
    padding: 0 $measure--basic $measure--basic;
}
