////
/// Statistics block
/// @group patterns
////

@mixin statistics-block($color: $color-red50) {
    @include media('<tablet') {
        padding: $measure--basic $measure--small2;
    }

    // Block renders as slider < tablet
    @include media('<tablet') {
        &[data-slider-render='tablet'] {
            .slider-block--mobile {
                [data-visible='false'] {
                    display: none;
                }
                .slider-block__control {
                    display: flex;
                    .slider-block__control__buttons--dot {
                        display: block;
                    }
                }
            }
        }
    }

    .statistics-block__heading {
        padding: $measure--basic;
        text-align: center;
    }

    .ksp__items {
        @include default-margin;
        max-width: none;
        padding: $measure--basic;
        margin: 0;
        text-align: center;
        grid-column-gap: 0;

        @include media('>=tablet') {
            display: grid;
        }

        // Generic properties for all statistics
        .ksp__item {
            padding: $measure--basic;
            margin: $measure--half 0;
            list-style-type: none;

            &:last-of-type {
                border-right: 0;
            }
            border-right: $border-width-default $border-style-solid $color-gray1;

            // Control vertical right border depending on device width
            @include media('<tablet') {
                border-right: 0;
                padding: 0;
                margin: 0;
            }
            h2 {
                @include h1style();
                font-weight: $font-weight-light;
                .animate--number__unit--post {
                    @include alphatype();
                    margin-left: $measure--quarter;
                }
            }
        }
        .ksp__item__overline {
            @include font-family('default');
            font-weight: $font-weight-book;
            font-size: $font-size--alpha;
            color: $color-gray7;
        }
        .ksp__item__pre-figure,
        .ksp__item__figure,
        .ksp__item__post-figure,
        .ksp__item__text-only {
            @include font-family('heading');
            font-weight: $font-weight-light;
            display: inline;
            color: $color;
        }
        .ksp__item__text {
            @include font-family('default');
            font-size: $font-size--alpha;
            font-weight: $font-weight-text;
            color: $color-gray10;
            margin: $measure--basic 0;
        }
        .ksp__item__icon {
            width: 52px; // Icon height matches text height.
            margin: 0 auto;
            display: block;
            @include media('<tablet') {
                margin: 0;
                display: inline;
            }
        }
        .ksp__item__heading--top-padding,
        .ksp__item__icon--top-padding {
            padding-top: 2.1rem; // Fills gap if no overline exists. Exact to match height if overline was present.
            @include media('<tablet') {
                padding-top: $measure--basic;
            }
        }

        // Animation
        .animation-incomplete {
            opacity: 0;
        }
        .animation-incomplete.animation-complete {
            opacity: 1;
            transition: opacity 1500ms;
        }

        // Control individual statistic width based on parent class
        &.ksp__items--2 {
            @include media('>=tablet', '<=desktop') {
                grid-template-columns: repeat(12, 1fr);

                .ksp__item {
                    &:nth-child(1) {
                        grid-column: 1 / 7;
                    }
                    &:nth-child(2) {
                        grid-column: 7 / 12;
                    }
                }
            }
            @include media('>desktop') {
                grid-template-columns: repeat(12, 1fr);

                .ksp__item {
                    &:nth-child(1) {
                        grid-column: 1 / 7;
                    }
                    &:nth-child(2) {
                        grid-column: 7 / -1;
                    }
                }
            }
        }

        &.ksp__items--3 {
            @include media('>=tablet', '<=desktop') {
                grid-template-rows: repeat(2, auto);
                grid-template-columns: repeat(12, 1fr);

                .ksp__item {
                    &:nth-child(1) {
                        grid-column: 2 / 7;
                    }
                    &:nth-child(2) {
                        grid-column: 7 / -1;
                        border-right: 0;
                    }
                    &:nth-child(3) {
                        grid-column: 4 / 10;
                        margin: 0 auto;
                    }
                }
            }
            @include media('>desktop') {
                grid-template-columns: repeat(12, 1fr);

                .ksp__item {
                    &:nth-child(1) {
                        grid-column: 1 / 5;
                    }
                    &:nth-child(2) {
                        grid-column: 5 / 9;
                    }
                    &:nth-child(3) {
                        grid-column: 9 / -1;
                    }
                }
            }
        }

        &.ksp__items--4 {
            @include media('>=tablet', '<=desktop') {
                grid-template-rows: repeat(2, auto);
                grid-template-columns: repeat(12, 1fr);

                .ksp__item {
                    &:nth-child(1),
                    &:nth-child(3) {
                        grid-column: 2 / 7;
                    }
                    &:nth-child(2),
                    &:nth-child(4) {
                        grid-column: 7 / 12;
                    }
                }

                .ksp__item {
                    &:nth-child(2) {
                        border-right: 0;
                    }
                }
            }
            @include media('>desktop') {
                grid-template-columns: repeat(12, 1fr);

                .ksp__item {
                    &:nth-child(1) {
                        grid-column: 1 / 4;
                    }
                    &:nth-child(2) {
                        grid-column: 4 / 7;
                    }
                    &:nth-child(3) {
                        grid-column: 7 / 10;
                    }
                    &:nth-child(4) {
                        grid-column: 10 / -1;
                    }
                }
            }
        }
    }
}

.statistics-block {
    @include statistics-block();
}
